body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    #display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }

  .reset {
    margin: 10px;
    float: right;
  }

  /* For mobile phones: */
[class*="square"] {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 60px;
  margin-right: 11px;
  margin-top: 11px;
  padding: 0;
  text-align: center;
  width: 60px;
}
  
  @media only screen and (min-width: 768px) {
    /* For desktop: */
  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 120px;
    margin-right: 11px;
    margin-top: 11px;
    padding: 0;
    text-align: center;
    width: 120px;
  }
}
  
  .square:focus {
    outline: none;
  }

  .square:hover {
   border-width: 3px;
   border-color: lightblue;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: row;
  }
  
  .game-info {
    margin-left: 20px;
  }
  